	module.exports = {"data":{"challengeNode":{"title":"Build a Nightlife Coordination App","description":["<strong>目标：</strong>开发一个功能类似于 <a href='http://whatsgoinontonight.herokuapp.com/' target='_blank'>http://whatsgoinontonight.herokuapp.com/</a>这个示例的全栈式 JavaScript 应用，并将其部署到 Heroku 上。","你应该为每一个项目创建一个 GitHub 仓库以及 Heroku 项目。如果你不记得如何操作，请查看<a href='/challenges/get-set-for-our-dynamic-web-application-projects'>https://freecodecamp.com/challenges/get-set-for-our-dynamic-web-application-projects</a>.","下面是你应该在这个项目完成的需求：","<strong>需求：</strong>作为一个未授权用户，我可以看到我附近的所有酒吧。","<strong>需求：</strong>作为一个已授权用户，我可以标记一个酒吧，表示我今晚将会去那里。","<strong>需求：</strong>作为一个已授权用户，如果我不再想去某个酒吧，我可以对该酒吧取消标记。","<strong>需求：</strong>作为一个未授权用户，在我登录后我不需要重新搜索附近的酒吧。","<strong>提示：</strong>请尝试使用 <a href='https://www.yelp.com/developers/documentation/v2/overview' target='_blank'>Yelp API</a> 查找地点。如果你使用 Yelp API，请在你的应用中声明你的使用。","当你完成以上需求,点击\"我已经完成了这个挑战\"按钮并输入你的 GitHub 仓库地址和 Heroku 地址。","你可以通过将你的项目分享到社交网络来获得反馈。"],"challengeType":4,"fields":{"blockName":"Take Home Projects","slug":"/coding-interview-prep/take-home-projects/build-a-nightlife-coordination-app"}}},"pathContext":{"challengeMeta":{"introPath":"","template":null,"required":[],"nextChallengePath":"/coding-interview-prep/take-home-projects/chart-the-stock-market","id":"bd7158d8c443eddfaeb5bdff"},"slug":"/coding-interview-prep/take-home-projects/build-a-nightlife-coordination-app"}}

